  --secondary-surface: hsl(0, 0%, 99.5%);
  --secondary-surface-border: hsl(0, 0%, 93%);
  --link: hsl(var(--hue), 100%, 40%);

  --editable: #fff;
  --editable-border: #ddd;
  --on-editable: #333;

  background: var(--surface);
  color: var(--on-surface);
}

h2 {
